body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width:100vw;
  height:auto;
  overflow: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.container {
  box-sizing: border-box;
  min-height: calc(100vh - 80px);
  height:auto;
  background-color: white;
  width: 100vw;
  padding: 0px 300px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: space-around;
          align-content: space-around;
  -webkit-flex-shrink:0;
          flex-shrink:0;
  overflow:scroll;
}

@-webkit-keyframes CharacterHover {
  from {
    box-shadow: 0;
  }
  to {
    box-shadow: 5px 3px 30px 1px black;
  }
}

@keyframes CharacterHover {
  from {
    box-shadow: 0;
  }
  to {
    box-shadow: 5px 3px 30px 1px black;
  }
}
img {
  width: 100%;
  height: 100%;
}
.character {
  border: 1px solid black;
  background-color: #619efb;
  height: 180px;
  width: 180px;
  overflow: hidden;
}
.character:hover {
  cursor: pointer;
  -webkit-animation: CharacterHover forwards .25s ease;
          animation: CharacterHover forwards .25s ease;
}

